@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@400;600;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@400;600;900&display=swap);
.jumbotron {
    padding-top: 200px !important;
    font-family: 'Patrick Hand SC', cursive;
    background-color: #f0e6db !important;
}
.jumbotronImage {
    max-width: 100%;
    webkit-box-shadow: -31px 10px 27px -26px rgba(148,143,148,1);
    box-shadow: -31px 10px 27px -26px rgba(148,143,148,1);
}

.jumbotron h2 {
    font-family: 'Lato', sans-serif !important;
}

@media screen and (max-width: 1000px) {
    .jumbotron {
        padding-top: 150px !important;
        margin: 0 auto;
    }
}


.carouselContainer{
    background-color: #e5e2da !important;
    width: 50% !important;
    margin: 0 auto !important;
}

.carousel-indicators > li{
    border-radius: 50% !important;
    width: 12px !important;
    height: 12px !important;
}
.navbar-text {
    font-family:'Sansita Swashed', cursive;
    bottom: 0 !important;
}
* {
  font-family: 'Roboto', sans-serif;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333333;
  font-size: 16px;
  margin: 0;
}
.page-heading{
    text-align: center;
  }

  .container-FundRequest{

    padding-top:15px;
    padding-bottom:15px;
}







.profile-text-description-container {

    padding-top: 15px;
    margin-left: 30%;
}
.profile-text-description {

    padding-top: 30px;
}

.auto-size-textarea{
    width: 100%;
}
.navbar {
  position: fixed !important;
  background-color: white;
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
}
.mr-auto {
  font-family: 'Lato', sans-serif;
}

.navbar-brand h1 {
  font-family: 'Sansita Swashed', cursive;
  font-size: 4.5rem;
}

.icon {
    font-size: 100px;
    display: inline-block;
    transform: scale(1.2);  
}

  
@media screen and (max-width: 940px) {
    div.hideonmobile {
      display: none;
    }

    .navbar-brand h1 {
      font-size: 2.5rem;
    }

    .icon {
      font-size: 50px;
      padding-right: 20px;
      display: inline-block;
      transform: scale(0.8);  
    }
  }
